@import "../../styles/mixins.scss";

.card{
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
    width: 100%;
  }

.order {
    @include textShadow;
}

.info {
    display: flex;
    margin: auto;

    text-align: center;
    list-style: none;
    padding: 0;
    
    & > li {
        margin: 10px;
    }
}
