.main {
    display: flex;

    max-width: 70vw;
    margin: 0 auto;
}

.left {
    display: flex;

    width: 30%;

    margin-top: 11vh;
    margin-left: auto;
}

.right {
    width: 65%;
    
    height: 90vh;
    overflow-y: overlay;
    overflow-x: hidden;
    
    display: flex;
    flex-direction: column;

    margin-top: 5vh;
    margin-left: auto;
    
    &::-webkit-scrollbar {
        width: 10px;
        background-color: #2f2f37;
        margin-left: 20px;
    }
    &::-webkit-scrollbar-thumb {
        background-color: #6b70aa;
    }
}


.nav {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-right: 60px;
}

.navChild {
    text-decoration: none;
    margin: 16px 0;
    color: #F2F2F3;

    &:visited {
        color: #8585AD;
    }
}

.navParagraph {
    margin-top: 48px;
    padding-right: 60px;

    color: #43414b;
}

.activeLink {
    color: #F2F2F3;
    
    &:visited {
        color: #F2F2F3;
    }
}