@import '../../styles/mixins.scss';

.main {
    display: flex;
}

.left {
    display: flex;
    justify-content: flex-end;
    width: 50%;
}

.right {
    display: flex;
    width: 50%;
}

.orders {
    height: 90vh;
    overflow-y: overlay;
    overflow-x: hidden;
    
    margin-top: 40px;

    &::-webkit-scrollbar {
        width: 10px;
        background-color: #2f2f37;
        margin-left: 20px;
      }
    &::-webkit-scrollbar-thumb {
        background-color: #6b70aa;
      }
}


.feedHeading {
    align-self: center;
}

.stats {
    display: flex;
    flex-direction: column;

    margin: 40px 30px 0 0;
}

.ordersStats {
    display: flex;

    margin-bottom: 20px;
}

.ordersReady {
    padding: 50px;
    margin-right: 100px;
}

.ordersPrep {
    padding: 50px;
}

.ordersAllTime {
    padding: 20px 50px 20px;
}

.ordersAllTimeNumber {
    @include textShadow;
}

.ordersToday {
    padding: 50px;
}

.ordersTodayNumber {
    @include textShadow;
}

.ordersDone {
    color: #00CED1;
}

.wrapper {
    display: flex;
    /* columns: 2 auto; */
}