@import '../../styles/mixins.scss';

.main {
    min-width: 50%;
}
.constructor {
    display: flex;
    flex-direction: column;
    gap: 10px;
    font-weight: 300;
    height: 60vh;
}

.middle {
    overflow-y: overlay;
    overflow-x: hidden;
    min-height: 10vh;

    @include sidebar;
}


.middleIngredients {
    display: flex;
    align-items: center;
}

.dragIcon {
    height: 24px;
    width: 24px;
}

.orderSection {
    display: flex;
    justify-content: flex-end;
    overflow: hidden;
}

.price {
    display: flex;
    align-items: center;
    font-size: 24px;
}